import React, { Component } from "react";
import {Helmet} from 'react-helmet'

class PageHelmet extends Component{
    render(){
        return(
            <React.Fragment>
                <Helmet>
                    <title>{this.props.pageTitle} Bauer Sites</title>
                    <meta name="description" content="Bauer comunicação agora é Bauer Sites. Criando sites e soluções para pequenas e médias empresas, além de profissionais liberais e autônomos em busca de um espaço na internet ." />
                </Helmet>
            </React.Fragment>
        )
    }
}


export default PageHelmet;
